.head {
  margin: 12px;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: blue;
  color: rgb(0, 119, 255);
  text-align: center;
  border: 4px solid blue;
  box-shadow: 0 0 4px blue, 0 0 4px blue, 0 0 4px blue, 0 0 4px blue,  0 0 4px blue,  0 0 4px blue,  0 0 4px blue,  0 0 4px blue,  0 0 4px blue,  0 0 4px blue;
}

.copy {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 20px;
  position: absolute;
  width: 100%;
  padding: 5px;
  bottom: 0;
  font-family: 'Courier New', Courier, monospace;
  background-color: aliceblue;
  text-transform: uppercase;
  text-align: center;
}

.head h1 {
  text-transform: uppercase;
  font-family: monospace;
  position: fixed;
  font-size: 50px;
  text-shadow: 0 0 6px white, 0 0 6px white, 0 0 6px white, 0 0 6px white, 0 0 6px white, 0 0 6px white, 0 0 6px white;
}

.head button {
  border-radius: 30px;
  cursor: pointer;
  padding: 15px;
  border: none;
  color: white;
  background-color: rgb(0, 255, 195);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-decoration: none;
  transition: all 0.5s;
}

.head button:hover {
  border: 4px solid green;
  box-shadow: 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green, 0 0 6px green;
}

#home {
  margin-right: 760px;
}

#audio-btn {
  margin-left: 500px;
}

.dash {
  border-radius: 20px;
  width: 50%;
  margin: 0 auto;
  background-color: #111;
  color: white;
  font-family: cursive;
  font-size: 20px;
  overflow: hidden;
}

.dash #player {
  float: left;
  margin-left: 30px;
}

.dash #opp {
  float: right;
  margin-right: 30px;
}

.dash #player.glow {
  color: cyan;
}

.dash #opp.glow {
  color: cyan;
}

#turns {
  font-size: 25px;
  color: white;
  margin: 10px;
  text-align: center;
  font-family: cursive;
}


.btn-game {
  border-radius: 20%;
  color: white;
  box-shadow: 0 0 10px blue, 0 0 10px blue, 0 0 10px blue, 0 0 10px blue, 0 0 10px blue, 0 0 10px blue;
  border: 6px solid blue;
  padding: 30px;
  background-color: #111;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  cursor: pointer;
  margin: 3px;
  width: 40px;
  height: 40px;
}

.btn-game:hover {
  opacity: 0.85;
}

.btn-game.win-btn {
  background-color: green;
}

.board-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 3px;
}

.board-row.top {
  margin-top: 20px;
}

